// props
const props = {
	// 
	value: {
		type: Array,
		default: () => (['00', '00', '00', '00'])
	},
	// 
	title: {
		type: String,
		default: ''
	},
	// 
	cancelText: {
		type: String,
		default: ''
	},
	// 
	canceColor: {
		type: String,
		default: '#666666'
	},
	// 
	confirmText: {
		type: String,
		default: ''
	},
	// 
	confirmColor: {
		type: String,
		default: '#2bb781'
	},
	// 
	segmentation: {
		type: String,
		default: '-'
	},
	//  stylescoped/deep/
	indicatorClass: {
		type: String,
		default: 'picker-view__indicator'
	},
	// 
	indicatorStyle: {
		type: String,
		default: ''
	},
}

// 
let range = [
	[],
	[],
	[],
	[]
];
for (let i = 0; i < 24; i++) {
	range[0].push(i >= 10 ? String(i) : `0${i}`);
	range[2].push(i >= 10 ? String(i) : `0${i}`);
}
for (let i = 0; i < 60; i++) {
	range[1].push(i >= 10 ? String(i) : `0${i}`);
	range[3].push(i >= 10 ? String(i) : `0${i}`);
}

export default {
	props,
	range
}